// @flow

import React from 'react'
import {graphql} from 'gatsby'
import {Disqus} from 'gatsby-plugin-disqus'
import moment from 'moment'

import PostHeader from './post-header'

import Layout from '../layout'
import SEO from '../seo'

import {MyArticle, HR, MySection, Img, ImgCaption, MyLink, Footer} from './styles'

type Props = {
  data: {
    markdownRemark: {|
      id: string,
      frontmatter: {|
        title: string,
        date: string,
        coverImage: {|
          src: string,
          owner: {|
            name: string,
            link: string,
          |},
          platform: {|
            name: string,
            link: string,
          |},
        |},
      |},
      excerpt: any,
      html: string,
    |},
    site: {
      siteMetadata: {|
        title: string,
        author: {|
          name: string,
        |},
        social: {|
          twitter: string,
          facebook: string,
          github: string,
        |},
        siteUrl: string,
      |},
    },
  },
  pageContext: {
    slug: string,
  },
  location: Location,
}

const Post = (props: Props) => {
  const {data, pageContext, location} = props

  const {
    markdownRemark: {
      frontmatter: {
        title,
        date,
        coverImage: {
          src,
          owner: {name: coverImageOwnerName, link: coverImageOwnerLink},
          platform: {name: coverImagePlatformName, link: coverImagePlatformLink},
        },
      },
      excerpt,
      html,
    },
    site: {
      siteMetadata: {
        title: siteTitle,
        author: {name},
        siteUrl,
      },
    },
  } = data

  const {slug} = pageContext

  const disqusConfig = {
    url: `${siteUrl}${slug}`,
    identifier: `${siteUrl}${slug}`,
    title,
  }

  return (
    <Layout location={location} title={siteTitle} posts={[]}>
      <SEO title={title} description={excerpt} />
      <MyArticle>
        <PostHeader
          title={title}
          date={moment(date)
            .local()
            .format('YYYY-MM-DD A hh:mm:ss')}
          author={name}
        />
        <Img src={src} alt={`Photo by ${coverImageOwnerName} on ${coverImagePlatformName}`} />
        <ImgCaption variant="body2">
          Photo by <MyLink href={coverImageOwnerLink}>{coverImageOwnerName}</MyLink> on{' '}
          <MyLink href={coverImagePlatformLink}>{coverImagePlatformName}</MyLink>
        </ImgCaption>
        <MySection dangerouslySetInnerHTML={{__html: html}} />
        <HR />
        <Footer>
          <Disqus config={disqusConfig} />
        </Footer>
      </MyArticle>
    </Layout>
  )
}

export default Post

export const pageQuery = graphql`
  query($slug: String!) {
    site {
      siteMetadata {
        title
        author {
          name
        }
        social {
          twitter
          facebook
          github
        }
        siteUrl
      }
    }
    markdownRemark(fields: {slug: {eq: $slug}}) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date
        coverImage {
          src
          owner {
            name
            link
          }
          platform {
            name
            link
          }
        }
      }
    }
  }
`
