// @flow

import styled from 'styled-components'
import {OutboundLink} from 'gatsby-plugin-google-gtag'

export const MyArticle = styled.article`
  max-width: ${props => props.theme.spacing(84)}px;
  margin-left: auto;
  margin-right: auto;
  font-family: 'Nanum Gothic', 'Courier New', Courier, monospace, Georgia, Cambria,
    'Times New Roman', Times, serif;
  font-size: 15px;
`

export const HR = styled.hr``

export const MySection = styled.section`
  img {
    max-width: 100%;
  }

  a {
    color: inherit;
  }
`

export const Img = styled.img`
  max-width: 100%;

  @media screen and (max-width: ${props => props.theme.spacing(84)}px) {
    margin-top: ${props => props.theme.spacing(5)}px;
  }

  @media screen and (min-width: ${props => props.theme.spacing(125)}px) {
    margin-top: ${props => props.theme.spacing(7)}px;
  }
`

export const ImgCaption = styled.p`
  margin-top: 10px;
  text-align: center;
  font-size: 13px;
  line-height: 20px;
`

export const MyLink = styled(OutboundLink)`
  /* text-decoration: none; */
  color: inherit;
`

export const Footer = styled.footer`
  @media screen and (max-width: ${props => props.theme.spacing(84)}px) {
    margin-top: 3em;
  }

  @media screen and (min-width: ${props => props.theme.spacing(125)}px) {
    margin-top: 4em;
  }
`
